.link {
  font-size: 15px;
  color: #85898b;
  text-decoration: none;
  padding: 2px 0;
  margin: 2px 0;
  display: flex;
  width: 100%;
  align-items: center; }
  .linkIcon {
    font-size: 20px;
    padding: 10px;
    margin: 0 10px 0 10px;
    color: #85898b; }
  .link:hover {
    background-color: #1e2024;
    cursor: pointer;
    color: white;
    border-radius: 6px; }
  .link:hover > .linkIcon {
    color: #ff9a60; }
