.topbar {
  display: flex;
  position: fixed;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  background-color: #32393d;
  padding: 0 15px; }

.start,
.end {
  width: 33.33%; }

.end {
  display: flex;
  justify-content: flex-end; }

.center {
  display: flex;
  width: 33.33%;
  align-items: center;
  justify-content: center; }

.logo {
  font-size: 28px;
  color: white;
  line-height: 60px;
  margin: 0 15px; }

.icon-btn-control {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 10px; }
  .icon-btn-control i {
    transition: 0.2s all;
    margin: 0 10px;
    font-size: 26px;
    color: #b3b3b3;
    cursor: pointer; }
    .icon-btn-control i:hover {
      color: #e6e6e6; }
  .icon-btn-control > i.icon-special {
    font-size: 26px;
    color: #ff9a60;
    cursor: pointer;
    transition: 0.2s all;
    border-radius: 50%; }
    .icon-btn-control > i.icon-special:hover {
      color: #ffaa7a;
      box-shadow: 0 7px 8px rgba(0, 0, 0, 0.5);
      animation-play-state: running; }

.sbar {
  display: flex;
  width: min-content;
  position: relative;
  justify-content: center;
  align-items: center; }

.search {
  font-size: 12, 5px;
  background-color: #1e2024;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  width: 200px;
  transition: 0.2s all;
  border: 1.5px solid transparent; }
  .search:focus {
    outline: none;
    border: 1.5px solid #ff9a60;
    width: 300px; }
  .search:focus ~ .search_icon_input {
    color: #cccccc; }

.search_icon_input {
  position: absolute;
  font-size: 15px;
  right: 7px;
  color: #666666; }

.isearch {
  align-self: center;
  margin: 10px;
  height: 40px;
  width: 80px;
  background-color: #ff9a60;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  z-index: 10;
  cursor: pointer;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.5);
  transition: 0.2s all; }
  .isearch:hover {
    background-color: #ffaa7a;
    box-shadow: 0 7px 8px rgba(0, 0, 0, 0.5); }
  .isearch i {
    position: relative;
    transition: 0.2s all;
    font-size: 28px;
    color: white;
    z-index: 11; }

@keyframes shadow-pulse {
  0% {
    box-shadow: 0 0 0 0px rgba(255, 255, 255, 0.3); }
  100% {
    box-shadow: 0 0 0 25px rgba(255, 255, 255, 0); } }
