* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

#wrapper {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 50px; }

.unselectable {
  user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  -o-user-select: none; }

.container {
  margin-top: 60px;
  display: flex;
  position: fixed;
  height: calc(100vh - 60px);
  flex-direction: column;
  padding: 15px;
  padding-bottom: 0; }

.containerRight {
  margin-top: 60px;
  display: flex;
  position: fixed;
  right: 0;
  height: calc(100vh - 60px);
  flex-direction: column;
  padding: 15px;
  padding-bottom: 0; }

.division {
  padding: 15px;
  width: 280px;
  background-color: #32393d;
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  margin-bottom: 15px; }

.scrollable {
  overflow-y: auto;
  height: 70px;
  flex-grow: 100; }

.growdiv {
  flex-grow: 100; }

.sidebar {
  width: 100%; }
  .sidebarEl {
    padding: 2px 0;
    font-size: 16px;
    color: white;
    margin: 2px 0;
    display: flex;
    width: 100%;
    align-items: center; }
    .sidebarEl:hover {
      background-color: #1e2024;
      cursor: pointer;
      color: white;
      border-radius: 6px; }
  .sidebar_icon {
    font-size: 15px;
    margin: 10px 15px;
    color: white; }

.user {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 0 15px 0; }

.ppicture {
  height: 70px;
  width: 70px;
  border-radius: 35px;
  margin-right: 15px;
  object-fit: cover; }

.online {
  position: absolute;
  top: 45px;
  left: 45px;
  height: 25px;
  width: 25px;
  border-radius: 100px;
  background-color: #66bb6a;
  border: solid #32393d 7px; }

.about {
  height: min-content; }

.username {
  display: block;
  color: white;
  font-size: 21px;
  line-height: 26px; }

.activity {
  display: block;
  color: #bdc3c7;
  font-size: 13px;
  line-height: 15px; }

.fppicture {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  object-fit: cover; }

.friend {
  text-decoration: none;
  position: relative;
  margin-right: 15px;
  padding: 10px;
  display: flex;
  align-items: center;
  border-radius: 5px;
  width: 100%; }
  .friend:hover {
    background-color: #1e2024; }
    .friend:hover .fonline {
      border: solid #1e2024 5px; }

.fonline {
  position: absolute;
  top: 40px;
  left: 40px;
  height: 20px;
  width: 20px;
  border-radius: 100px;
  background-color: #66bb6a;
  border: solid #32393d 5px; }

.fusername {
  display: block;
  color: white;
  font-size: 14, 5px;
  line-height: 15px; }

.factivity {
  display: block;
  color: #bdc3c7;
  font-size: 13px;
  line-height: 15px; }

.fabout {
  margin-left: 10px; }

.yellow {
  background-color: #cf8300; }

.red {
  background-color: #c03b3b; }

.green {
  background-color: #66bb6a; }

.black {
  background-color: #585e6a; }

.gamediv {
  margin-right: 15px;
  align-items: center;
  display: flex;
  padding: 10px; }
  .gamediv:hover {
    background-color: #1e2024;
    border-radius: 5px; }

.tab-control {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px; }
  .tab-control span {
    text-align: center;
    padding: 13px 0;
    font-weight: 500;
    font-size: 14px;
    width: 100%;
    border-radius: 8px;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-bottom: 3px solid #41454e;
    cursor: pointer;
    transition: 0.2s all;
    color: #bfbfbf; }
    .tab-control span:hover {
      color: white; }

span:focus {
  border-bottom: 3px solid #ff9a60 !important;
  color: white !important; }

.gameicon {
  width: 50px;
  height: 50px;
  margin-right: 10px;
  border-radius: 50%;
  object-fit: cover; }

.gamename {
  color: white;
  font-size: 15px;
  flex-grow: 100; }

.playicon {
  width: 30px;
  height: 30px;
  background-color: #ff9a60;
  border-radius: 50%;
  margin-right: 10px;
  cursor: pointer; }
  .playicon:hover {
    background-color: #ffaa7a;
    box-shadow: 0 4px 5px rgba(0, 0, 0, 0.5); }
  .playicon i {
    margin: 10px 10px 10px 11px;
    font-size: 10px;
    color: white; }

.scrollable::-webkit-scrollbar-track {
  border-radius: 100px;
  background-color: #32393d; }

.scrollable::-webkit-scrollbar {
  width: 7px;
  background-color: #32393d;
  border-radius: 100px; }

.scrollable::-webkit-scrollbar-thumb {
  background-color: #1e2024;
  border-radius: 100px; }
